.about {
  display: grid;
  gap: 30px;

  @media (min-width: @tablet-width) {
    gap: 40px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
}

.about__img-container {
  @media (min-width: @desktop-width) {
    padding-right: 20px;
  }
}

.about__img {
  width: 100%;
  height: 268px;
  object-fit: cover;
  object-position: top;

  @media (min-width: @tablet-width) {
    height: 400px;;
  }

  @media (min-width: @desktop-width) {
    height: 635px;
  }
}

.about__content {
  .wrapper();

  @media (min-width: @desktop-width) {
    align-self: center;

    max-width: unset;
    margin-right: 0;
    margin-left: 0;
    padding-left: 0;
  }
}

.about__title {
  margin-bottom: 20px;

  font-family: Forum, sans-serif;
  font-size: 25px;
  font-weight: 400;
  line-height: 32px;

  color: @color-font-orange;

  @media (min-width: @tablet-width) {
    font-size: 40px;
    line-height: 52px;
  }

  @media (min-width: @desktop-width) {
    font-size: 50px;
    line-height: 65px;
  }
}

.about__decription {
  font-size: 15px;
  font-weight: 400;
  line-height: 21px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:last-of-type {
    margin-bottom: 30px;
  }

  @media (min-width: @tablet-width) {
    font-size: 17px;
    line-height: 24px;

    &:last-of-type {
      margin-bottom: 40px;
    }
  }
}

.about__btn {
  width: 100%;
  padding: 16px 10px 20px;

  @media (min-width: @tablet-width) {
    width: 260px;
  }
}
